/* You can add global styles to this file, and also import other style files */
:root {
    --fontFamily1: 'Poppins', sans-serif;
    --fontFamily2: 'Open Sans', sans-serif;
    --mainColor: #a80203;
    --optionalColor: #000000;
    --whiteColor: #ffffff;
    --blackColor: #252525;
    --paragraphColor: #727695;
    --fontSize: 15px;
    --transition: .5s;
}
body {
    background-color: var(--whiteColor);
    padding: 0;
    margin: 0;
    font: {
        family: var(--fontFamily1);
        size: var(--fontSize);
    };
}
a {
    text-decoration: none;
    transition: var(--transition);
    color: var(--blackColor);

    &:hover, &:focus {
        color: var(--mainColor);
        text-decoration: none;
    }
}
input, button, a {
    outline: 0 !important;
}
p {
    margin-bottom: 15px;
    line-height: 1.7;
    color: var(--paragraphColor);
    font: {
        family: var(--fontFamily2);
        size: var(--fontSize);
    };
    &:last-child {
        margin-bottom: 0;
    }
}
.d-table {
    width: 100%;
    height: 100%;

    &-cell {
        vertical-align: middle;
    }
}
img {
    max-width: 100%;
    height: auto;
}
.ptb-100 {
    padding: {
        top: 100px;
        bottom: 100px;
    };
}
.pt-100 {
    padding-top: 100px;
}
.pb-100 {
    padding-bottom: 100px;
}
.ptb-70 {
    padding: {
