        bottom: 70px;
    };
}
.pt-70 {
    padding-top: 70px;
}
.pb-70 {
    padding-bottom: 70px;
}
.mb-30 {
    margin-bottom: 30px;
}
.mt-30 {
    margin-top: 30px;
}
.bg-F7F9FB {
    background-color: #F7F9FB;
}
.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
    color: var(--blackColor);
    font: {
        family: var(--fontFamily1);
    };
    &:last-child {
        margin-bottom: 0;
    }
}

/*default-btn*/
.default-btn {
    transition: var(--transition);
    display: inline-block;
    padding: 13px 25px 12px 55px;
    position: relative;
    background-color: var(--mainColor);
    color: var(--whiteColor);
    border: {
        width: 2px;
        style: solid;
        color: var(--mainColor);
        radius: 1px;
    };
    font: {
        size: 14.5px;
        weight: 700;
    };
    .label, .icon-arrow {
        backface-visibility: hidden;
        transform: translateZ(0);
        perspective: 1000;
    }
    .label {
        display: inline-block;
        transition: transform .5s cubic-bezier(0.86, 0, 0.07, 1);
    }
    .icon-arrow {
        color: var(--whiteColor);
        top: 11px;
        transition: transform .5s cubic-bezier(0.86, 0, 0.07, 1), opacity .4s cubic-bezier(0.86, 0, 0.07, 1);
        font-size: 25px;
    }
    .icon-arrow {
        &.before {
            left: 20px;
            position: absolute;
            transform-origin: left center;
        }
        &.after {
            opacity: 0;
            position: absolute;
            right: 20px;
            transform: translateX(75%) scaleX(0.1);
            transform-origin: right center;
        }
    }
    &:hover, &:focus {
        background-color: var(--optionalColor);
        color: var(--whiteColor);
        border-color: var(--optionalColor);

        .label {
            transform: translateX(-32px);
        }
        .icon-arrow {
            &.before {
                opacity: 0;
                transform: translateX(-75%) scaleX(0.1);
            }
            &.after {
                opacity: 1;
                transform: translateX(0) scaleX(1);
            }
        }
    }
}

/*section-title*/
.section-title {
    text-align: center;
    position: relative;
    margin: {
        top: -5px;
        bottom: 60px;
    };
    .sub-title {
        text-transform: uppercase;
        display: block;
        color: var(--mainColor);
        font-weight: 500;
        margin-bottom: 10px;
    }
    h2 {
        margin-bottom: 12px;
        font: {
            size: 37px;
            weight: 700;
        };
    }
    p {
        max-width: 620px;
        line-height: 1.8;
        margin: {
            left: auto;
            right: auto;
        };
    }
    .default-btn {
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        z-index: 1;
    }
    &.text-left {
        h2 {
            max-width: 620px;
            margin-bottom: 0;
        }
        p {
            margin: {
                bottom: 0;
                left: 0;
                top: 15px;
                right: 0;
            };
        }
    }
}

/*form-control*/
.form-control {
    height: 48px;
    padding: 0 0 0 12px;
    line-height: initial;
    color: var(--blackColor);
    background-color: var(--whiteColor);
    border: 1px solid #e6e9fc;
    border-radius: 3px;
    transition: var(--transition);
    font: {
        size: var(--fontSize);
        weight: 400;
    };
    &::placeholder {
        color: #c4c5d0;
    }
    &:focus {
        box-shadow: unset;
        border-color: #d7d9eb;
    }
}
textarea.form-control {
    height: auto !important;
    padding-top: 12px;
}

