/*default-btn*/
.default-btn {
  transition: var(--transition);
  display: inline-block;
  padding: 13px 25px 12px 55px;
  position: relative;
  background-color: var(--mainColor);
  color: var(--whiteColor);
  border-width: 2px;
  border-style: solid;
  border-color: var(--mainColor);
  border-radius: 1px;
  font-size: 14.5px;
  font-weight: 700;
}
.default-btn .label, .default-btn .icon-arrow {
  backface-visibility: hidden;
  transform: translateZ(0);
  perspective: 1000;
}
.default-btn .label {
  display: inline-block;
  transition: transform 0.5s cubic-bezier(0.86, 0, 0.07, 1);
}
.default-btn .icon-arrow {
  color: var(--whiteColor);
  top: 11px;
  transition: transform 0.5s cubic-bezier(0.86, 0, 0.07, 1), opacity 0.4s cubic-bezier(0.86, 0, 0.07, 1);
  font-size: 25px;
}
.default-btn .icon-arrow.before {
  left: 20px;
  position: absolute;
  transform-origin: left center;
}
.default-btn .icon-arrow.after {
  opacity: 0;
  position: absolute;
  right: 20px;
  transform: translateX(75%) scaleX(0.1);
  transform-origin: right center;
}
.default-btn:hover, .default-btn:focus {
  background-color: var(--optionalColor);
  color: var(--whiteColor);
  border-color: var(--optionalColor);
}
.default-btn:hover .label, .default-btn:focus .label {
  transform: translateX(-32px);
}
.default-btn:hover .icon-arrow.before, .default-btn:focus .icon-arrow.before {
  opacity: 0;
  transform: translateX(-75%) scaleX(0.1);
}
.default-btn:hover .icon-arrow.after, .default-btn:focus .icon-arrow.after {
  opacity: 1;
  transform: translateX(0) scaleX(1);
}

