  display: none;
}
blockquote::before, .blockquote::before {
  color: #efefef;
  position: absolute;
  left: 50px;
  top: -50px;
  z-index: -1;
  content: "\ed67";
  font-family: "boxicons" !important;
  font-weight: normal;
  font-style: normal;
  font-variant: normal;
  font-size: 135px;
}
blockquote::after, .blockquote::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 3px;
  background-color: var(--mainColor);
  margin-top: 20px;
  margin-bottom: 20px;
}

